import React, { Component, Suspense } from "react";
import { toast } from 'react-toastify';
import $ from 'jquery';
import { connect } from "react-redux";
import FileSaver from "file-saver";
import moment from 'moment'
import { Nav, Tab } from 'react-bootstrap';
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { Uppy } from "@uppy/core";
import { ugcActionRquest, getLastUgcRequest } from '../../../../../Services/Actions/ugcRightsActions'
import { loadAssetsDataWithDispatch } from '../../../../../Services/Actions/assetsActions'
import { getURLFromVideo, showAddAccountAlert, GET_NETWORK_ICON } from '../../../../../Services/Utils/utils'
import { IS_LIVE } from '../../../../../Services/Api'
import { tinyRequest } from '../../../../../Services/Actions/tinyUrlAction'
import { getAccountList } from '../../../../../Services/Actions/connectionsActions'
//import FileInput from "@uppy/react/lib/FileInput";
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { addUserAccount } from '../../../../../Services/Actions/addFeedAction';

const GetRightsGenralRequest = React.lazy(() => import('./GetRightsSteps/GetRightsGenralRequest'));
const GetRightsSocialAccount = React.lazy(() => import('./GetRightsSteps/GetRightsSocialAccount'));
const GetRightsCopyUrl = React.lazy(() => import('./GetRightsSteps/GetRightsCopyUrl'));
const GetRightsPendingAndExpire = React.lazy(() => import('./GetRightsStatus/GetRightsPendingAndExpire'));
const GetRightsDownload = React.lazy(() => import('./GetRightsStatus/GetRightsDownload'));
const GetRightsRejected = React.lazy(() => import('./GetRightsStatus/GetRightsRejected'));

const uppyConfig = {
    uppyKey: '0701d3d7f8d94e92a65a9cba388b1d96',
    uppyBucket: 'cloud.taggbox.com',
    uppyAwsKey: '47ABVORKG52UYE1KB28S',
    uppyAwsSecret: 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v',
    companionUrl: IS_LIVE ? 'https://app.taggbox.com' : 'https://test.taggbox.com'
}


const channel = new BroadcastChannel('connect');



class AssetsModalAside extends Component {
    constructor(props) {
        super(props)
        this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyConfig.uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyConfig.uppyBucket,
                            key: uppyConfig.uppyAwsKey,
                            secret: uppyConfig.uppyAwsSecret,
                            path: 'postimage/${file.id}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })

        this.uppyPostEdit = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['application/pdf', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'] } })
            .use(Transloadit, {
                target: '#a',
                params: {
                    auth: {
                        key: uppyConfig.uppyKey
                    },
                    steps: {
                        store: {
                            robot: "/wasabi/store",
                            bucket_region: "us-west-1",
                            bucket: uppyConfig.uppyBucket,
                            key: uppyConfig.uppyAwsKey,
                            secret: uppyConfig.uppyAwsSecret,
                            path: 'test/${file.id}'
                        }
                    }
                },
                waitForEncoding: true
            })
            .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
            .use(Url, { target: Uppy.Dashboard, companionUrl: uppyConfig.companionUrl })
    }
    state = {
        step: 1,
        client_logo: null,
        client_logo_name: null,
        comment: 'We loved your creative brilliance! Can we showcase your incredible work and give you the recognition you deserve? ✨📸🙌. Please approve our request so we can use your content in our marketing campaign.',
        lastComment: 'We loved your creative brilliance! Can we showcase your incredible work and give you the recognition you deserve? ✨📸🙌. Please approve our request so we can use your content in our marketing campaign.',
        tncName: "TermsAndConditions.docx",
        tncUrl: "TermsAndConditions.docx",
        tnc: false,
        logoModal: false,
        fileModal: false,
        activeKey: 'ugc-socialAccount',
        connectedAccount: null,
        commentError: null,
        tncUrlError: null,
        tncError: null,
        requestedRespone: null,
        rightsVia: '',
        accountList: [], selectedAccount: null,
        hashValue: `yes`,
        requested: false,
        isRequesting: false

    }

    onClickToChange = (step) => event => {
        if (this.state.step == 1) {
            if (this.state.rightsVia && String(this.state.rightsVia).length > 0) {
                this.setState({
                    step, activeKey: "ugc-genralRequest"
                })
            }
            else {
                toast.error("Please select the mode for Right Request", { autoClose: 2000 })
            }



        }
        else if (this.state.step == 2) {
            if (this.state.rightsVia == "viaDM") {
                if (this.state.comment && this.state.tncUrl && this.state.tnc) {
                    // this.setState({ step, activeKey: "ugc-socialAccount" })
                    this.onClicktoSendRequest({ step, activeKey: "ugc-copyUrl" })
                }
                else {
                    if (!this.state.comment || String(this.state.comment).length == 0) this.setState({ commentError: true })
                    if (!this.state.tncUrl) this.setState({ tncUrlError: true })
                    if (!this.state.tnc) this.setState({ tncError: true })
                }

            }
            else if (this.state.rightsVia == "viaComments") {
                const { selectedAccount } = this.state
                if (this.state.comment) this.setState({ comment: `${this.state.comment} Just reply with #${this.state.hashValue} to @${selectedAccount && Object.keys(selectedAccount).length > 0 ? selectedAccount.userName : ""} if you're up for it, and you can find all the nitty-gritty details in our bio. Thanks a bunch!`, }, () => this.setState({ step, activeKey: "ugc-copyUrl" }))
                else {
                    if (!this.state.comment || String(this.state.comment).length == 0) this.setState({ commentError: true })
                }
            }


        }
        else if (this.state.step == 3) {

        }

    }
    onClickToRemoveLogo = event => this.setState({
        client_logo: null, client_logo_name: null,
    })
    componentWillMount() {
        this.onLoadLastRequestData()
    }

    componentDidMount() {
        this.uppyPostEdit.on('transloadit:result', (stepName, result) => {
            var imgpath = result.url.split('/');
            var imglength = imgpath.length;
            var filename = imgpath[imglength - 1];
            let { tncName, tncUrl } = this.state;
            tncUrl = result.id
            tncName = result.name
            this.setState({ tncName, tncUrl, selectedFile: result.id, filename: `https://cloud.taggbox.com/test/${result.id}`, selected: `https://cloud.taggbox.com/test/${result.id}` }, () => {
                setTimeout(() => {
                    this.uppyPostEdit.cancelAll()
                    this.setState({ fileModal: false })
                }, 300)
            })

        })

        this.uppy.on('transloadit:result', (stepName, result) => {
            let { client_logo, client_logo_name } = this.state

            client_logo = `https://cloud.taggbox.com/postimage/${result.id}`
            client_logo_name = result.name
            this.setState({ client_logo, client_logo_name, selectedlogoFile: result.id, logofilename: `https://cloud.taggbox.com/postimage/${result.id}`, logoselected: `https://cloud.taggbox.com/postimage/${result.id}` }, () => {
                setTimeout(() => {
                    this.uppy.cancelAll()
                    this.setState({ logoModal: false })
                }, 300)
            })

        })
        channel.addEventListener("message", this.readAuthenticationCode);

        this.onLoadAddAccount()
    }
    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId, type } = response;
        if (type == "social-account") {
            addUserAccount(networkId, data, () => {
                this.onLoadAddAccount()
            }, 0, 0);
        }
    }
    onLoadAddAccount = () => {

        var self = this;
        getAccountList().then((response) => {
            const { responseData } = response.data
            const { modalAssetData } = self.props;
            if (responseData && responseData.length > 0) {
                const updateData = responseData.filter((item) => {
                    if (item.Network.id == modalAssetData.networkId && !(item.ConnectedAccounts.api == 3 || item.ConnectedAccounts.api == 4)) {
                        if (String(item.Network.name).includes("Instagram")) return item
                        else return item.Network.id == modalAssetData.networkId
                    }

                })
                var newData = []
                updateData.map((newItem) => {
                    newItem.ConnectedAccounts.child.map((childItem) => {
                        newData.push({ ...newItem, userName: childItem.ConnectedAccounts.value2 })
                    })
                })





                self.setState({ accountList: newData, selectedAccount: newData[0] }, () => {
                    var { comment, selectedAccount, rightsVia } = this.state
                    if (rightsVia == "viaComments") {
                        var matches = comment.match(/@([^@\s]+)/g);
                        if (selectedAccount) {
                            if (matches) {
                                var values = matches.map(match => match.replace('@', ''));
                                if (values && values.length > 0 && values.filter((item) => `@${item}` == matches[0]).length > 0) {

                                    comment = comment.replace(`${matches[0]}`, `@${selectedAccount.userName}`)

                                    this.setState({ comment })
                                }
                            } else {
                                if (comment && String(comment).includes("@")) {
                                    comment = comment.replace(`${matches[0]}`, `@${selectedAccount.userName}`)

                                    this.setState({ comment })

                                }
                            }
                        }

                    }

                })
            }


        })
    }
    onLoadLastRequestData = () => {
        var self = this;
        getLastUgcRequest().then((response) => {
            const { result } = response.data
            var client_logo = {}; var comment = {}; var tncURL = {}
            if (result.client_logo && String(result.client_logo).length > 0) {
                client_logo = { client_logo: result.client_logo }
            }
            if (result.custom_message && String(result.custom_message).length > 0) {
                comment = { comment: result.custom_message, lastComment: result.custom_message }
            }

            if (result.custom_term_type && String(result.custom_term_type).length > 0) {
                tncURL = { tncName: result.custom_terms, tncUrl: result.custom_term_type }
            }
            self.setState({ ...client_logo, ...comment, ...tncURL })
        })
    }


    onClickClearFile = event => {
        this.setState({ tncName: "", tncUrl: "" })
    }

    componentWillUnmount() {
        this.uppy.close();
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    termsFileDownload = () => {
        //var blob = new Blob(["Hello, world!"], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(baseUrl('media/downloads/Terms&ConditionsforuseofUGC.docx'), "Terms&ConditionsforuseofUGC.docx");
    }
    modalClose = (step) => event => {
        if (step == 2) this.setState({ step: 1, activeKey: "ugc-socialAccount" })
        else this.props.modalClose(false)
    }
    onChangeComment = event => this.setState({ comment: event.target.value })
    onClicktnc = event => {
        const { tnc } = this.state
        this.setState({ tnc: !tnc, tncError: null })
    }

    onLogoClose = event => {
        this.uppy.cancelAll()
        this.setState({ logoModal: false })
    }
    onLogoOpen = event => this.setState({ logoModal: true }, () => {
        $(".uppy-Dashboard-close").click(() => {
            this.uppy.cancelAll()
            this.setState({ logoModal: false })
        })
    })

    onFileOpen = event => {
        this.setState({ fileModal: true }, () => {
            $(".uppy-Dashboard-close").click(() => {
                this.uppyPostEdit.cancelAll()
                this.setState({ fileModal: false })
            })
        })

    }

    onFileClose = () => {
        this.uppyPostEdit.cancelAll()
        this.setState({ fileModal: false })
    }
    onSelected = (key) => {
        this.setState({ activeKey: key })
    }

    sendNow = event => {
        const { modalAssetData, isBulkStatus, bulkRightsData } = this.props;
        const { comment, tncUrl, tncName, client_logo, connectedAccount, rightsVia, selectedAccount, hashValue, requested } = this.state;
        if (!requested) {
            var requestedAccount = {}
            if (connectedAccount && connectedAccount.data.length > 0) {
                requestedAccount = connectedAccount.data[0]
            }
            var self = this;
            var assetsData = []
            if (isBulkStatus) {
                bulkRightsData.map((grItem) => {
                    assetsData.push({ "referenceId": grItem.referenceId, "username": grItem.username, "media": grItem.publishImage, "video": getURLFromVideo(grItem.mediaFile), ...requestedAccount, link: grItem.link, author: grItem.picture, postId: grItem.postId, "collection": grItem.collection })
                })

            }
            else {
                assetsData.push({ "referenceId": modalAssetData.referenceId, "username": modalAssetData.username, "media": modalAssetData.publishImage, "video": getURLFromVideo(modalAssetData.mediaFile), ...requestedAccount, link: modalAssetData.link, author: modalAssetData.picture, postId: modalAssetData.postId, "collection": modalAssetData.collection })
            }
            const requestedData = {
                "feedId": modalAssetData.feedId,
                "custom_message": comment,
                "networkId": modalAssetData.networkId,
                "brandlink": client_logo ? client_logo : "",
                "handle": selectedAccount && Object.keys(selectedAccount).length > 0 && selectedAccount.userName ? selectedAccount.userName : "",
                "hashtag": hashValue,
                "termslink": tncUrl,
                "custom_term_type": tncName,
                "assets": assetsData,
                "rightsVia": rightsVia == 'viaComments' ? 1 : 0
            }
            this.setState({ isRequesting: true })
            ugcActionRquest(requestedData).then((response) => {
                self.setState({ requested: true, isRequesting: false })
                self.props.loadAssetsDataWithDispatch()
                navigator.clipboard.writeText(comment);
                setTimeout(() => window.open(modalAssetData.link, '_blank'), 400)
            })
        }
        else {
            toast.error("Already rights raise requested.", { autoClose: 2000 })
        }

    }
    onClicktoSendRequest = (updateStep) => {
        ///viaComments
        const { modalAssetData, isBulkStatus, bulkRightsData } = this.props;
        const { comment, tncUrl, tncName, client_logo, connectedAccount, rightsVia, selectedAccount, hashValue } = this.state;
        var requestedAccount = {}
        if (connectedAccount && connectedAccount.data.length > 0) {
            requestedAccount = connectedAccount.data[0]
        }
        var self = this;
        var assetsData = []
        if (isBulkStatus) {
            bulkRightsData.map((grItem) => {
                assetsData.push({ "referenceId": grItem.referenceId, "username": grItem.username, "media": grItem.publishImage, "video": getURLFromVideo(grItem.mediaFile), ...requestedAccount, link: grItem.link, author: grItem.picture, postId: grItem.postId, "collection": grItem.collection })
            })

        }
        else {
            assetsData.push({ "referenceId": modalAssetData.referenceId, "username": modalAssetData.username, "media": modalAssetData.publishImage, "video": getURLFromVideo(modalAssetData.mediaFile), ...requestedAccount, link: modalAssetData.link, author: modalAssetData.picture, postId: modalAssetData.postId, "collection": modalAssetData.collection })
        }
        const requestedData = {
            "feedId": modalAssetData.feedId,
            "custom_message": comment,
            "networkId": modalAssetData.networkId,
            "brandlink": client_logo ? client_logo : "",
            "handle": selectedAccount && Object.keys(selectedAccount).length > 0 && selectedAccount.userName ? selectedAccount.userName : "",
            "hashtag": hashValue,
            "termslink": tncUrl,
            "custom_term_type": tncName,
            "assets": assetsData,
            "rightsVia": rightsVia == 'viaComments' ? 1 : 0
        }
        this.setState({ isRequesting: true })
        ugcActionRquest(requestedData).then((response) => {
            self.props.loadAssetsDataWithDispatch()
            if (rightsVia == 'viaDM') {
                tinyRequest(modalAssetData.link).then((tResponse) => {
                    const { data } = tResponse.data
                    navigator.clipboard.writeText(comment + ' Post : ' + data.tiny_url + ' Form Link : ' + response.data.bitlylink);
                    if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
                    else setTimeout(() => window.open(data.tiny_url, '_blank'), 400)
                    self.setState({ requestedRespone: response.data, ...updateStep, isRequesting: false })
                }).catch((ex) => {
                    navigator.clipboard.writeText(comment + ' Post : ' + modalAssetData.link + ' Form Link : ' + response.data.bitlylink);
                    if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
                    else setTimeout(() => window.open(modalAssetData.link, '_blank'), 400)
                    self.setState({ requestedRespone: response.data, ...updateStep, isRequesting: false })

                })
            }


        })
    }
    onUpdateData = (provider, data) => {
        this.setState({ connectedAccount: { provider, data } })
    }
    rightsViaOption = event => {
        this.setState({
            rightsVia: event,
            comment: event == "viaComments" ? `Hey there! 😄 We stumbled upon this amazing photo of yours, and all we could say was, "WOW." We want to share this post with our community.
    We'd be absolutely thrilled if you'd let us feature your photo on our website and social media. It's all about celebrating your talent! 🌟
    `: this.state.lastComment
        })
    }
    onChangeSelectedAccount = (item) => event => {
        let { comment, selectedAccount } = this.state
        var matches = comment.match(/@([^@\s]+)/g);
        if (item) {
            if (matches) {
                var values = matches.map(match => match.replace('@', ''));
                if (values && values.length > 0 && values.filter((item) => item == selectedAccount.userName).length > 0) {
                    comment = comment.replace(`@${selectedAccount.userName}`, `@${item.userName}`)
                }
            } else {
                if (comment && String(comment).includes("@")) {
                    comment = comment.replace(`@${selectedAccount.userName}`, `@${item.userName}`)
                }
            }
        }

        this.setState({ selectedAccount: item && Object.keys(item).length > 0 ? item : item, comment }, () => {
            const { selectedAccount } = this.state;
            const { modalAssetData } = this.props
            if (selectedAccount == null) {
                showAddAccountAlert({ id: modalAssetData.networkId, name: GET_NETWORK_ICON(modalAssetData.networkId) }, "social-account")
            }

        })
    }
    hashChange = event => {
        let { comment, hashValue } = this.state
        var matches = comment.match(/#([^#\s]+)/g);
        const hashTextValue = event.target.value
        if (hashTextValue && String(hashTextValue)) {
            if (matches) {
                var values = matches.map(match => match.replace('#', ''));
                if (values && values.length > 0 && values.filter((item) => item == hashValue).length > 0) {
                    comment = comment.replace(`#${hashValue}`, `#${String(hashTextValue).trim()}`)
                    this.setState({ hashValue: String(hashTextValue).trim(), comment: comment })
                }
            } else {
                if (comment && String(comment).includes("#")) {
                    comment = comment.replace(`#${hashValue}`, `#${String(hashTextValue).trim()}`)
                    this.setState({ hashValue: String(hashTextValue).trim(), comment: comment })
                }
            }
        }
    }
    render() {
        const { modalAssetData } = this.props
        const { step, activeKey, connectedAccount, requestedRespone, comment, rightsVia, accountList, selectedAccount, hashValue, isRequesting } = this.state
        return modalAssetData && Object.keys(modalAssetData).length > 0 ? <div className="rights_modal_sidebar position-relative">
            <div className="rights_modal_aside_wrap">
                {modalAssetData.ugcRights == null ? <Tab.Container activeKey={activeKey} onSelect={this.onSelected}>
                    <div className="ugc-wizard-nav">
                        <Nav variant="nav" className="bg-light justify-content-between row gx-0">
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 1 || step == 2 || step == 3 ? `active` : ``}`} eventKey="ugc-socialAccount" onClick={event => this.setState({ step: 1 })}>
                                    <span className="d-flex me-1 text-success">
                                        <i className="ri-checkbox-circle-fill icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Choose Method</span>

                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 2 || step == 3 ? `active` : `disabled`}`} eventKey="ugc-genralRequest">
                                    <span className="d-flex me-1">
                                        <i className="checkIcoFilled ri-checkbox-circle-fill icon-5 text-success"></i>
                                        <i className="checkIco ri-checkbox-circle-line icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Generate Request</span>
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className={`py-3 py-xl-4 block-center ${step == 3 ? `active` : `disabled`}`} eventKey="ugc-copyUrl">
                                    <span className="d-flex me-1">
                                        <i className="checkIcoFilled ri-checkbox-circle-fill icon-5 text-success"></i>
                                        <i className="checkIco ri-checkbox-circle-line icon-5"></i>
                                    </span>
                                    <span className="fw-bold text-nowrap">Preview</span>
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </div>
                    <Tab.Content>

                        <Tab.Pane eventKey="ugc-socialAccount">
                            <Suspense fallback={<></>}>
                                <GetRightsSocialAccount modalAssetData={modalAssetData} onUpdateData={this.onUpdateData} connectedAccount={connectedAccount} rightsViaOption={this.rightsViaOption} rightsVia={rightsVia} />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-genralRequest">
                            <Suspense fallback={<></>}>
                                <GetRightsGenralRequest state={this.state} onChangeComment={this.onChangeComment} onClicktnc={this.onClicktnc} onClickClearFile={this.onClickClearFile} uppyPostEdit={this.uppyPostEdit} uppy={this.uppy} onLogoClose={this.onLogoClose} onLogoOpen={this.onLogoOpen} onClickToRemoveLogo={this.onClickToRemoveLogo} onFileOpen={this.onFileOpen} onFileClose={this.onFileClose} rightsVia={rightsVia} />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-copyUrl">
                            <Suspense fallback={<></>}>
                                <GetRightsCopyUrl key={activeKey} modalAssetData={modalAssetData} requestedRespone={requestedRespone} comment={comment} rightsVia={rightsVia} accountList={accountList} selectedAccount={selectedAccount} onChangeSelectedAccount={this.onChangeSelectedAccount} hashValue={hashValue} hashChange={this.hashChange} sendNow={this.sendNow} isRequesting={isRequesting} />
                            </Suspense>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container>
                    :
                    modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 1 ?
                        <Suspense fallback={<></>}>
                            <GetRightsPendingAndExpire days={parseInt(30) - (parseInt((parseInt((moment().diff(moment(modalAssetData.ugcRights.created), 'days'))))) + parseInt(1))} expireStatus={true} modalAssetData={modalAssetData} />
                        </Suspense>
                        : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 2 ?
                            <Suspense fallback={<></>}>
                                <GetRightsDownload modalAssetData={modalAssetData} />
                            </Suspense> : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 3 ? <Suspense fallback={<></>}>
                                <GetRightsRejected modalAssetData={modalAssetData} onUpdateRejectedRightDetail={this.props.onUpdateRejectedRightDetail} />
                            </Suspense> : null}
            </div>
            {modalAssetData.ugcRights == null ? <div className="rights_modal_actions text-center modal-footer w-100">
                {step == 1 || step == 2 ? <><button type="button" className="btn btn-secondary me-2" onClick={this.modalClose(step)}>{step == 1 ? `Cancel` : step == 2 ? `Back` : ``}</button>
                    <button type="button" className={`btn btn-primary ${isRequesting ? `spinner disabled` : ``}`} onClick={this.onClickToChange(step == 1 ? 2 : step == 2 ? 3 : 4)}>Next Step</button>
                </> : null}


                {step == 3 ? <button type="button" className="btn btn-secondary me-2" onClick={this.modalClose(step)}>Done</button> : null}
            </div> : null}
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return {

    }
}
const mapDispatchToProps = dispatch => {
    return {

        loadAssetsDataWithDispatch: () => dispatch(loadAssetsDataWithDispatch()),




    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AssetsModalAside);